<template>
  <el-table :data="tableData" border style="width: 100%; height: 100%" size="small" class="apptable"
    :row-class-name="tableRowClassName" show-overflow-tooltip="true">
    <el-table-column prop="createtime" label="时间" width="180" />
    <el-table-column prop="host" label="域名" width="180" />
    <el-table-column prop="uri" label="路径" width="180" />
    <el-table-column prop="ip" label="IP" width="180" />
    <el-table-column prop="ip_addr" label="IP归属地" width="180" />
    <el-table-column prop="agent" label="平台/浏览器" min-width="180" />
  </el-table>
</template>
  
<script>
import "element-plus/es/components/message/style/css";

export default {
  data() {
    return {
      tableData: [] // 使用普通数组初始化 tableData
    };
  },
  methods: {
    tableRowClassName({ row }) {
      if (row.host == "xsrc.shop") {
        return "warning-row";
      }
    }
  },
  mounted(){
    setTimeout(() => {
      this.tableData = this.$store.state.AppTable.table
    }, 500);
  }
};
</script>


<style>
.el-table .warning-row {
  --el-table-tr-bg-color: var(--el-color-danger-light-9);
}

.el-table .success-row {
  --el-table-tr-bg-color: var(--el-color-success-light-9);
}

.el-table {
  border-radius: 4px;
}
</style>
  