<template>
  <div class="tabs">
    <div class="header">
        <div class="btn">
            <div class="host"  @click="btnvalue = 0"><el-text>域名</el-text></div>
            <div class="ip"  @click="btnvalue = 1"><el-text>ip</el-text></div>
            <div class="btncover" :class="{active: btnvalue}"></div>
        </div>
        <el-input v-model="input" placeholder="Please input" />
        <div class="svg"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024" data-v-ea893728=""><path fill="currentColor" d="M77.248 415.04a64 64 0 0 1 90.496 0l226.304 226.304L846.528 188.8a64 64 0 1 1 90.56 90.496l-543.04 543.04-316.8-316.8a64 64 0 0 1 0-90.496z"></path></svg></div>
    </div>
    <div class="list"></div>
  </div>
</template>

<script>
export default {
    name:"AppTabs",
    data(){
        return{
            input:'',
            btnvalue:0
        }
    }

}
</script>

<style scoped lang="less">
    .tabs{
        width: 100%;
        display: flex;
        flex-direction: column;
        &>.header{
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            &>.btn{
                display: flex;
                width: 60px;
                margin-right: 10px;
                white-space: nowrap;
                border: solid 1px #dcdfe6;
                border-radius: 4px;
                position: relative;
                overflow: hidden;
                &>div{
                    width: 50%;
                    height: 30px;
                    cursor: pointer;
                    user-select: none;
                    z-index: 1;
                    &>span{
                        display: block;
                        line-height: 30px;
                        text-align: center;
                    }
                }

                &>.btncover{
                    display: block;
                    width: 50%;
                    height: 30px;
                    top: 0;
                    left: 0;
                    right: auto;
                    position: absolute;
                    background-color: #c6e2ff;
                    z-index: 0;
                    transition: all 0.2s linear;
                }

                &>.active{
                    transform: translateX(100%);
                }
            }
            &>.el-input{
                width: 220px;
            }
            &>.svg{
                width: 30px;
                height: 30px;
                padding: 8px;
                background-color: #c6e2ff;
                border-radius: 4px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-left: auto;
                cursor: pointer;
                &>svg{
                    color: #606266;
                }
            }
        }

        
        &>.list{
                width: 100%;
                max-height: 400px;
                min-height: 100px;
                border: solid 1.5px #dcdfe6;
                border-radius: 4px;
                margin-top: 10px;
            }
    }
</style>