import { createRouter, createWebHistory } from 'vue-router';
import AppHealth from '@/components/home/AppHealth.vue';
import AppStatistics from '@/components/home/AppStatistics.vue'
import AppTable from '@/components/monitor/AppTable.vue';
import AppPanel from '@/components/monitor/AppPanel.vue'

// 定义路由
const routes = [
  {
    path: '/',
    components: {
        main:AppHealth,
        panel:AppStatistics
    }
  },
  {
    path: '/monitor',
    components: {
        main:AppTable,
        panel:AppPanel
    }
  }
];

// 创建 router 实例
const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;