import { createStore } from 'vuex';
import axios from "axios";

// 创建一个新的 store 实例
const store = createStore({
  actions: {
    changePanel(context,value){
        context.commit('CHANGEPANEL',value)
    },

    // 域名检测
    getAppHealthTable(context){
      axios.get("https://api.nonpage.com/index.php/api/query/health", {
        params: {
          auth: "yimeiqi",
        },
      }).then((res) => {
        context.commit('GETAPPHEALTHTABLE',res.data)
      });
    },
    // monitor下面的apptable表格数据
    getAppTableTable(context){
      axios.get("https://api.nonpage.com/index.php/api/query/totaiwan", {
        params: {
          auth: "yimeiqi",
          limit: 500,
        },
      }).then((res) => {
        if(res.data.code === 1){
          context.commit('GETAPPTABLETABLE',res.data.data)
        }else{
          context.commit('GETAPPTABLEABLE',[])
        }
      });
    },

    // 近7天数据
    getPast7days(context){
      axios.get('https://api.nonpage.com/index.php/api/query/totaiwancount',{
        params:{
          time:'week'
        }
      }).then(res=>{
        context.commit('GETPAST7DAYS',res.data)
      })
    },

    // 近24小时数据
    getPast24hours(context){
      axios.get('https://api.nonpage.com/index.php/api/query/totaiwancount',{
        params:{
          time:'day'
        }
      }).then(res=>{
        context.commit('GETPAST24HOURS',res.data)
      })
    },

    // 近24小时饼状图
    getPast24Pie(context){
      axios.get('https://api.nonpage.com/index.php/api/query/totaiwanpie',{
        params:{
          auth:'yimeiqi'
        }
      }).then(res => {
        context.commit('GETPAST24PIE',res.data)
      })
    }


  },
  mutations: {
    CHANGEPANEL(state,value){
        state.panelValue = value
    },
    // monitor下面的apptable表格数据
    GETAPPHEALTHTABLE(state,value){
      state.AppHealth.table = value
    },
    GETAPPTABLETABLE(state, value){
      state.AppTable.table = value
    },

    // 近7天数据
    GETPAST7DAYS(state,value){
      state.Past7days = value;
    },

    // 近24小时数据
    GETPAST24HOURS(state,value){
      state.Past24hours = value;
    },

    // 近24小时饼状图
    GETPAST24PIE(state, value){
      state.Past24Pie = value;
    }

  },
  state:{
    panelValue:750,
    AppHealth:{
      table:[]
    },
    AppTable:{
      table:[]
    },
    // 近7天数据
    Past7days: [],
    
    // 近24小时数据
    Past24hours: [],

    // 近24小时饼状图
    Past24Pie: [],
  }
});

export default store;
