<template>
    <el-table :data="tableData" border style="width: 100%; height: 100%" size="small" class="apptable"
        :row-class-name="tableHealth" show-overflow-tooltip="true">
        <el-table-column prop="id" label="索引" width="180" />
        <el-table-column prop="domain" label="域名" width="180" />
        <el-table-column prop="domain_health" label="域名状态" width="180" />
        <el-table-column prop="createtime" label="更新时间" />
    </el-table>
</template>

<script>
export default {
    name: 'AppHealth',
    data() {
        return {
            tableData: []
        }
    },
    methods: {
        tableHealth({ row }) {
            if (row.domain_health == "台湾屏蔽") {
                return "danger-row"
            }
        }
    },
    mounted() {
        setTimeout(() => {
            this.tableData = this.$store.state.AppHealth.table
        }, 1000);
    },
    watch: {
        '$store.state.AppHealth.table'(newValue) {
            this.tableData = newValue;
        }
    }
}
</script>

<style>
.el-table .danger-row {
    --el-table-tr-bg-color: var(--el-color-danger-light-3);
}

.apptable tbody tr:hover>td {
    background-color: transparent !important;
}
</style>