<template>
  <div class="common-layout">
    <el-container>
      <el-header>
        <AppHeader></AppHeader>
      </el-header>
      <el-container>
        <el-aside width="200px">
          <AppAside></AppAside>
        </el-aside>
        <el-main class="animated-aside"><router-view name="main"></router-view></el-main>
        <el-aside :width="`${$store.state.panelValue}px`" class="animated-aside"><router-view
            name="panel"></router-view></el-aside>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import AppHeader from "@/components/AppHeader.vue";
import AppAside from "./components/AppAside.vue";

export default {
  components: {
    AppHeader,
    AppAside,
  },
  mounted() {
    this.$store.dispatch('getAppTableTable');
    this.$store.dispatch('getAppHealthTable');

    // 近24小时
    this.$store.dispatch('getPast24hours');
    // 近7天
    this.$store.dispatch('getPast7days');

    // 定时更新
    setInterval(() => {
      this.$store.dispatch('getPast24hours');
      this.$store.dispatch('getPast7days');
    }, 5000);

    // 近24饼状图
    this.$store.dispatch('getPast24Pie');
    setInterval(() => {
      this.$store.dispatch('getPast24Pie');
    }, 30000);

  },
  watch: {
    // 使用字符串形式监听路由变化
    '$route'(to) {
      if (to.path === '/') {
        this.$store.commit('CHANGEPANEL', 750);
      } else {
        this.$store.commit('CHANGEPANEL', 370);
      }
    }
  }
};
</script>

<style lang="less">
* {
  box-sizing: border-box;
}

.animated-aside {
  transition: width 0.5s ease-in-out;
}


body {
  margin-top: 20px;
  overflow: hidden;
  background-color: #e5e9f2;
}

.common-layout {
  width: 100%;
  height: 100vh;

  .el-main {
    height: calc(100vh - 80px);
  }
}</style>