<template>
    <v-chart class="chart" :option="option" autoresize />
</template>

<script setup>
import { use, } from 'echarts/core';
import { useStore } from 'vuex';
import { CanvasRenderer } from 'echarts/renderers';
import { PieChart } from 'echarts/charts';
import {
    TitleComponent,
    TooltipComponent,
    LegendComponent,
} from 'echarts/components';
import VChart, { THEME_KEY } from 'vue-echarts';
import { ref, provide, watchEffect } from 'vue';

use([
    CanvasRenderer,
    PieChart,
    TitleComponent,
    TooltipComponent,
    LegendComponent,
]);

const store = useStore();

provide(THEME_KEY, 'light');

const option = ref({
    title: {
        text: '近24小时域名访问量',
        left: 'center',
    },
    tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b} : {c} ({d}%)',
    },
    legend: {
        orient: 'vertical',
        left: 'left',
        // 初始化为空数组，稍后用watchEffect填充
        data: [],
    },
    series: [
        {
            name: '近24小时访问量',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            // 初始化为空数组，稍后用watchEffect填充
            data: [],
            emphasis: {
                itemStyle: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                },
            },
        },
    ],
});

// 使用watchEffect来响应store.state.Past24Pie的变化
watchEffect(() => {
    option.value.legend.data = store.state.Past24Pie.map(item => item.host);
    option.value.series[0].data = store.state.Past24Pie.map(item => ({
        value: item.count,
        name: item.host
    }));
});
</script>

  
<style scoped>
.chart {
    height: 260px;
    width: 730px;
    background-color: #fff;
    border: 1px solid var(--el-border-color);
    border-radius: 4px;
}
</style>
  