<template>
    <v-chart class="chart" :option="option" @legendselectchanged="onLegendSelectChanged" autoresize />
</template>
  
<script setup>
import { ref, provide, watchEffect, } from 'vue';
import { useStore } from 'vuex';
import "echarts";
import { use } from 'echarts/core';
import { CanvasRenderer } from 'echarts/renderers';
import { PieChart } from 'echarts/charts';
import {
    TitleComponent,
    TooltipComponent,
    LegendComponent,
} from 'echarts/components';
import VChart, { THEME_KEY } from 'vue-echarts';

use([
    CanvasRenderer,
    PieChart,
    TitleComponent,
    TooltipComponent,
    LegendComponent,
]);

provide(THEME_KEY, 'light');

const store = useStore();
const option = ref({
    title: {
        text: '访问量'
    },
    animationDuration: 1000,    // 动画时间
    tooltip: {
        trigger: 'axis'
    },
    legend: {
        data: ['近24小时', '近7天'],
        selectedMode: 'single'
    },
    grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
    },
    xAxis: {
        type: 'category',
        boundaryGap: false,
        data: []
    },
    yAxis: {
        type: 'value'
    },
    series: [
        {
            name: '近24小时',
            type: 'line',
            stack: 'Total',
            data: [],
            label: {
                show: true, // 显示标签
                position: 'top', // 标签的位置，'top' 表示位于点的上方
                formatter: '{c}' // 标签内容格式器，'{c}' 表示当前点的值
            }
        },
        {
            name: '近7天',
            type: 'line',
            stack: 'Total',
            data: [],
            label: {
                show: true, // 显示标签
                position: 'top', // 标签的位置，'top' 表示位于点的上方
                formatter: '{c}' // 标签内容格式器，'{c}' 表示当前点的值
            },
            lineStyle: {
                color: '#ff9f7f', // 折线颜色，这里以红色为例
                width: 2, // 折线宽度
                type: 'solid' // 折线类型，'solid' 表示实线
            },
            itemStyle: {
                color: '#ff9f7f', // 数据点颜色，与折线颜色相同
                // 其他数据点样式配置...
            },
        }
    ]
});

const current = ref(0);

watchEffect(() => {
    option.value.xAxis.data = current.value ? store.state.Past7days.map(item => item.day) : store.state.Past24hours.map(item => item.hour);
    option.value.series[0].data = store.state.Past24hours.map(item => item.count);
    option.value.series[1].data = store.state.Past7days.map(item => item.daily_count);
});

// Legend选择变化时的处理函数
const onLegendSelectChanged = (event) => {
    const selected = event.selected;
    const selectedName = Object.keys(selected).find(name => selected[name]);

    if (selectedName === '近24小时') {
        // option.value.xAxis.data = store.state.Past24hours.map(item => item.hour);
        current.value = 0
    } else if (selectedName === '近7天') {
        // option.value.xAxis.data = store.state.Past7days.map(item => item.day);
        current.value = 1
    }
};

</script>
  
  
<style lang="less" scoped>
.chart {
    max-height: 400px;
    width: 730px;
    background-color: #fff;
    border: 1px solid var(--el-border-color);
    border-radius: 4px;
}
</style>
  